import { Document, RangeSelection, Text } from '@ali/4ever-cangjie';
/**
 * 选区是否闭合且选区内的text为空
 * @param range
 * @param document
 */
export default function isCollapsedInEmptyText(range: RangeSelection, document: Document): boolean {
  if (range.isCollapsed) {
    const focusNode = document.getNode(range.focus.key);
    return !!focusNode && Text.isText(focusNode) && !focusNode.text;
  }
  return false;
}
